@import url("https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap");
* {
  font-family: "Bree Serif", serif;
}
.quote {
  width: 80%;
  margin: 20px auto;
  border: 0px solid blue;

  font-family: 'Bree Serif, serif'
}
.quote > img {
  margin: auto;
}
#home {
  width: 90%;
  margin: auto;
  margin-top: 130px;
  font-family: serif;
  /* color: #243D25; */
  color: #4c3575;
  border: 0px solid gold;

}

#myImg {
  border-radius: 30px;
  width: 80%;
  margin-right: 50px;

  /* height: 450px; */
}

#myDetailsDiv {
  width: 100%;
  margin-left: 60px;
  text-align: justify;
  font-family: "Bree Serif", serif;

}
.img_txt_wrapper {
  display: flex;
  border: 0px solid black;
  align-items: center;
  justify-content: space-between;
}
