#icon:hover{
  background-color:rgb(132,116,160);

}
@keyframes type {
  0% {
    width: 0ch;
  }
  100% {
    width: 22ch;
  }
}

#myName {
  margin-bottom: 7px;
  font-size: 50px;
 animation: type 4s steps(35) infinite alternate;
  overflow: hidden;
  border: 0px solid blue;
   animation-timing-function: linear;
  font-family: "Bree Serif", serif;
  white-space: nowrap;
}

#title {
  margin-bottom: 30px;
  font-size: 42px;

 animation: type 4s steps(35) infinite alternate;;
  overflow: hidden;
  animation-timing-function: linear;
  font-family: "Bree Serif", serif;
  white-space: nowrap;
  border: 0px solid teal;
}

@keyframes type-backward {
  0% {
    width: 22ch;
  }
  100% {
    width: 0ch;
  }
}




#detail {
  line-height: 26px;
  font-family: serif;
  font-size: 18px;
  border: 0px solid pink;
}
/* <-------Back to top buttun------> */
.bttBtn{
    background-color: rgb(6, 103, 187);
    color: red;
}

.bttBtn:hover{
    background-color: red;
}
/* <-------Back to top buttun------> */
@media screen and (max-width: 1124px) and (min-width: 768px) {
  #home {
    width: 90%;
    margin-top: 150px;
  }

  #myDetailsDiv {
    width: 90%;
    margin: auto;
    margin-left: 20px;
  }

  #myName {
    margin-bottom: 10px;
    font-size: 32px;
  }

  #myImg {
    border-radius: 30px;
    width: 50%;
    /* height: 380px; */
    margin: auto;
  }
  .quote {
    width: 80%;
    margin: auto;
  }
  .quote > img {
    margin: auto;
  }
  #title {
    font-size: 20px;
  }

  #detail {
    font-size: 18px;
  }
}

@media screen and (max-width: 767px) and (min-width: 300px) {
  #home {
    width: 90%;
    margin-top: 150px;
  }
  .img_txt_wrapper {
    margin: auto;
    margin-top: 20px;
    width: 80%;
    flex-direction: column;
  }
  #myDetailsDiv {
    width: 100%;
    margin: auto;
  }
  .quote {
    width: 80%;
    margin: auto;
  }
  #myName {
    margin-bottom: 10px;
    font-size: 25px;
  }

  #myImg {
    border-radius: 30px;
    /* width: 62%; */
    /* height: 380px; */
    margin: auto;
    border: 0px solid red;
  }

  #title {
    font-size: 18px;
  }

  #detail {
    font-size: 18px;
  }
}